import { LookupItem } from '../services/lookup.service';
import { Base } from './base.model';
import { Entidade } from './entidade.model';
import { PlanejamentoObjetivo } from './planejamento-objetivo.model';
import { Unidade } from './unidade.model';

export class Planejamento extends Base {
    public unidade?: Unidade;
    public entidade?: Entidade;
    public objetivos?: PlanejamentoObjetivo[];
    public planejamento_superior?: Planejamento;
    
    public data_arquivamento: Date | null = null;  /* Data de arquivamento */
    public data_inicio: Date = new Date(); /* Data de incio do planejamento */
    public data_fim: Date | null = null; /* Data final do planejamento */
    public nome: string = ""; /* Nome do planejamento institucional */
    public missao: string = ""; /* Misso da Instituio/Unidade */
    public visao: string = ""; /* Viso da Instituio/Unidade */
    public valores: LookupItem[] = []; /* Valores da Instituio/Unidade */
    
    public unidade_id: string | null = null; /* Unidade  qual est vinculado o planejamento institucional */
    public entidade_id: string | null = null; /* Entidade  qual est vinculado o planejamento institucional */
    public planejamento_superior_id: string | null = null; /* Planejamento hierarquicamente superior ao qual o atual planejamento est vinculado */

    public constructor(data?: any) { super(); this.initialization(data); }
}
